import React, { Component } from 'react';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import { Mutation } from 'react-apollo';
import { InMemoryCache } from 'apollo-cache-inmemory';
import { createUploadLink } from 'apollo-upload-client';
import { ApolloClient } from "apollo-client";
import { ApolloProvider } from "react-apollo";
import gql from "graphql-tag";
import { Col, Row } from "react-bootstrap";
import { Typography } from "@material-ui/core";
import Loader from 'react-loader-spinner';
import VideocamIcon from '@material-ui/icons/Videocam';

import './video.style.css';
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css"

const apolloCache = new InMemoryCache()
let testToken;
if (localStorage.getItem('jwt')) {
    testToken = JSON.parse(localStorage.getItem('jwt')).BearerTokenResponse.access_token || '';
}
const token = testToken;
const uploadLink = createUploadLink({
    uri: 'https://spreago.blacksquaressolutions.com/graphql',
    headers: {
        authorization: token ? `Bearer ${token}` : ""
    }
})

const client = new ApolloClient({
    cache: apolloCache,
    link: uploadLink
})


export default class Video extends Component {
    constructor(props) {
        super(props);
        this.state = {
        }
    }
    
    stopWebCamera =()=>{
        var video = document.querySelector('video');
            const stream = video.srcObject;
            if(stream){
                const tracks = stream.getVideoTracks();
                tracks[0].stop()
                tracks.forEach(function(track) {
                    track.stop();
                });
                video.srcObject = null;
            }
    }

    componentWillUpdate(props){
        if(props !== this.props){
            this.setState({ ...this.state, bioId: props.data.id , enableWebCam : props.enableWebCam});
        }
      
    }
    maxtime = parseInt(this.props.data.maximum_time + "000");

    componentDidMount() {
        // get the bio id 
       this.setState({ ...this.state, bioId: this.props.data.id, data: this.props.data })

        let file, that = this;

        const constraints = {
            audio: true,
            video: {
                width: 980,
                height: 520
            }

        };
        // const video = document.querySelector('video');

        // start the cam 
        async function init(constraints) {
            try {
                const stream = await navigator.mediaDevices.getUserMedia(constraints);
                handleSuccess(stream);
            } catch (e) {
                console.error('navigator.getUserMedia error:', e);
                errorMsgElement.innerHTML = `This Page have no permission for accessing camera.`;
            }
        }

        if (this.props.enableWebCam === 1 ) {
           // init(constraints);
            document.querySelector('.btn-group-rec-style').classList.remove('d-none');
        }
        // document.querySelector('.question-next').addEventListener("click",function(){
        //     init(constraints);
        //     document.querySelector('.btn-group-rec-style').classList.remove('d-none');
        // });
        let mediaRecorder;
        let recordedBlobs;
        let firstBlob;
        // let base64data;
        //let sourceBuffer;
        let timeLimit;

        const errorMsgElement = document.querySelector('span#errorMsg');
        const recordedVideo = document.querySelector('video');
        const recordButton = document.querySelector('button#record');

        // record button click
        recordButton.addEventListener('click', () => {
            if (recordButton.classList.contains('rec')) {
                let flag = 0;
                init(constraints)
                // setInterval(function() { 
                //     startRecording();
                // }, 1000);

            } else {
                //when stop recording 
                window.clearTimeout(timeLimit);
                document.querySelector('.recording-text').classList.add('d-none');
                document.querySelector('.circle-light-play').classList.remove('d-none');
                document.querySelector('.start-rec-text').classList.remove('d-none');
                document.querySelector('.play-text').classList.remove('d-none');
                document.querySelector('.btns-group').classList.add('btn-group-rec-style');
                document.querySelector('.start-text').classList.add('d-none');
                recordButton.classList.add('rec');
                recordButton.classList.remove('stop');
                playButton.disabled = false;
                stopRecording();
                // downloadButton.disabled = false;
            }
        });
        // play recorded video 
        const playButton = document.querySelector('button#play');
        playButton.addEventListener('click', () => {
            const superBuffer = new Blob(recordedBlobs, { type: 'video/webm' });
            recordedVideo.src = null;
            recordedVideo.srcObject = null;
            recordedVideo.src = window.URL.createObjectURL(superBuffer);
            recordedVideo.controls = true;
            recordedVideo.muted = false;
            document.querySelector('.start-rec-text').classList.add('d-none');
            document.querySelector('.btns-group').classList.remove('btn-group-rec-style');
            document.querySelector('.play-text').classList.add('d-none');
            recordedVideo.play();
        });


        // function that push every moment recorded in array 
        function handleDataAvailable(event) {
            // console.log('handleDataAvailable', event);
            if (event.data && event.data.size > 0 && event.data && event.data.size < 2) {
                firstBlob = event.data;
            }
            if (event.data && event.data.size > 0) {
                recordedBlobs.push(event.data);
            }
        }
        // start recording a video
        function startRecording() {
            recordedBlobs = [];
            let timeout = that.maxtime;
            let options = { mimeType: 'video/webm;codecs=vp9' };
            if (!MediaRecorder.isTypeSupported(options.mimeType)) {
                console.error(`${options.mimeType} is not Supported`);
                errorMsgElement.innerHTML = `${options.mimeType} is not Supported`;
                options = { mimeType: 'video/webm;codecs=vp8' };
                if (!MediaRecorder.isTypeSupported(options.mimeType)) {
                    console.error(`${options.mimeType} is not Supported`);
                    errorMsgElement.innerHTML = `${options.mimeType} is not Supported`;
                    options = { mimeType: 'video/webm' };
                    if (!MediaRecorder.isTypeSupported(options.mimeType)) {
                        console.error(`${options.mimeType} is not Supported`);
                        errorMsgElement.innerHTML = `${options.mimeType} is not Supported`;
                        options = { mimeType: '' };
                    }
                }
            }
            try {
                recordedVideo.muted = true;
                recordedVideo.controls = false;
                mediaRecorder = new MediaRecorder(window.stream, options);
                timeLimit = setTimeout(function () {
                    stopRecording();
                    document.querySelector('.recording-text').classList.add('d-none');
                    document.querySelector('.circle-light-play').classList.remove('d-none');
                    document.querySelector('.start-rec-text').classList.remove('d-none');
                    document.querySelector('.play-text').classList.remove('d-none');
                    document.querySelector('.btns-group').classList.add('btn-group-rec-style');
                    document.querySelector('.start-text').classList.add('d-none');
                    recordButton.classList.add('rec');
                    recordButton.classList.remove('stop');
                    playButton.disabled = false;
                }, timeout);
            } catch (e) {
                console.error('Exception while creating MediaRecorder:', e);
                errorMsgElement.innerHTML = `Exception while creating MediaRecorder: ${JSON.stringify(e)}`;
                return;
            }

            // console.log('Created MediaRecorder', mediaRecorder, 'with options', options);
            recordButton.classList.remove('rec');
            recordButton.classList.add("stop");
            playButton.disabled = true;
            // downloadButton.disabled = true;

            // convert blobs to file
            mediaRecorder.onstop = (event) => {
                //console.log('Recorder stopped: ', event);
                // console.log('Recorded Blobs: ', recordedBlobs);
                var blob = new Blob(recordedBlobs, { type: "video/webm" });
                file = new File([blob], "videoname", {
                    type: "video/webm",
                });
                // download video 
                // var reader = new FileReader();
                // reader.readAsDataURL( blob );
                // reader.onloadend = function() {
                //     var base64data = reader.result;
                //     var dummy = document.createElement('a');
                //     dummy.href = base64data;
                //     dummy.download = "newVideo" ;
                //     dummy.click( );
                //     console.log(base64data);
                //     console.log( dummy );
                // }
                that.setState({ ...that.state, file: file });
                
                if (that.state.file) {
                    document.getElementById("btn-save-video").click();
                }
            };

            mediaRecorder.ondataavailable = handleDataAvailable;
            mediaRecorder.start(10); // collect 10ms of data
            // console.log('MediaRecorder started', mediaRecorder);
    }

        function stopRecording() {
            mediaRecorder.stop();
        }

        function handleSuccess(stream) {
            recordButton.disabled = false;
            //console.log('getUserMedia() got stream:', stream);
            window.stream = stream;

            const gumVideo = document.querySelector('video');
            gumVideo.srcObject = stream;
            recordButton.classList.remove('rec');
            recordButton.classList.add('stop');
            //when recording
            document.querySelector('.start-rec-text').classList.add('d-none');
            document.querySelector('.circle-light-play').classList.add('d-none');
            document.querySelector('.btns-group').classList.remove('btn-group-rec-style');
            document.querySelector('.play-text').classList.add('d-none');
            document.querySelector('.recording-text').classList.remove('d-none');
            startRecording();
        }
    
    
}

    render() {
        if(client){
        return (
            <div>
                <ApolloProvider client={client}>
                    <Mutation mutation={UPLOAD_FILE_STREAM}>
                        {(videoUpload, { data, loading }) => {
                            return (
                                <form encType={'multipart/form-data'}>
                                    <button
                                        hidden
                                        type="button"
                                        id="btn-save-video"
                                        className="btn-orange"
                                        onClick={() => {
                                            const file = this.state.file;
                                            const id = this.state.bioId;
                                            file && videoUpload({
                                                variables: { file: file, question_id: id }
                                            }).then((res, loading) => {
                                                if (res.data.videoUpload.Status.code) {
                                                    let uploaded = document.querySelector("[data-uploaded=\"" + id + "\"]");
                                                    uploaded.classList.remove("d-none");
                                                    this.stopWebCamera();
                                                }

                                            }).catch((err) => this.setState({ ...this.state, ConnectionErorr: "Please try again" }));
                                        }}>save</button>
                                    {loading &&
                                        <div className="loading">
                                            <Loader
                                                type="Oval"
                                                color="#E1631C"
                                                height={100}
                                                width={100}
                                            />
                                        </div>
                                        }
                                </form>)
                        }
                        }
                    </Mutation>
                    <div className="video-container">
                        <video muted autoPlay width="100%" height="" className="rounded bg-dark" controlslist="nodownload" disablepictureinpicture="false" ></video>
                        <div className="btns-group btn-group-rec-style d-none">
                            <div className="circle-light-rec d-inline-block">
                                <button id="record" className="rec text-center border-0 btn-rec-position" ></button>
                                <span className="start-rec-text">
                                    <span className="start-text text-light">Start</span>
                                    <span className="play-text d-none text-light">Play</span>
                                    <span className="time-limitation text-light"></span>
                                </span>
                                <span className="recording-text text-danger d-none">
                                    <strong>Recording</strong>
                                </span>
                            </div>
                            <button id="play" className="circle-light-play  text-center border-0 d-none" disabled><PlayArrowIcon className="text-dark btn-play-position" /></button>
                        </div>
                    </div>
                    <div style={{color:"red",textAlign:"center"}}>
                        <span id="errorMsg"></span>
                    </div>
                    <Row className="m-0">
                        <Col xs={12} className="pb-3">
                                    <Typography variant="subtitle1" className="text-danger">{this.state.ConnectionErorr}</Typography>
                            </Col>
                    </Row>
                </ApolloProvider>
            </div>
        );
    }
}
}

