const UPLOAD_FILE_STREAM = gql`
mutation(
    $file        : Upload !
    $question_id : Int    !
) {
    videoUpload (
        Inputs : {
            question:{
                id : $question_id
            }
            videoInputs : {
                file : $file
            }
        }
    ) {
        Status {
            code
            title
            message
            check
        }
        question{
            title
            bio{
                title
            }
            video{
                url
                frame{
                    url
                  }
                type{
                    name
                }
            }
        }
    }
}
`;
