import React, { Component } from 'react';
import { withRouter } from 'react-router';
import { graphql } from 'react-apollo';
import * as compose from 'lodash.flowright';
import { gql } from 'apollo-boost';
import Video from '../video/video.component';
import { Container, Col, Card, Row } from 'react-bootstrap';
import { Button , Typography} from '@material-ui/core';
import CropOriginalIcon from '@material-ui/icons/CropOriginal';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';

// import dots from './images/more.png';
//import imgPlaceHolder from '../../images/image-place.png';
import './record.style.css';

class Record extends Component {
    constructor() {
        super();
        this.state = {
            enableWebCam : 1,
            questions : ''
        }
    }
    // getQuestion = (props, state) => {
    //     let that = this;
    //     let list = [], tab = [];
    //     list = document.getElementById("tab-content-container");
    //     tab = list.querySelectorAll(".tab-content .tab-pane");
    //     const clearSetinterval = (functionName) => {
    //         clearInterval(functionName);
    //     }

    //     var recordInterval = window.setInterval(function () {
    //         if (tab[2].classList.contains('active')) {
    //             // let leftList = document.getElementById("left-tabs-example");
    //             // let nav_items = leftList.querySelectorAll(".nav-item a");
    //             // let bioId = nav_items[0].getAttribute("data-id");
    //             that.setState({ ...that.state, enableWebCam : 1 });
    //             clearSetinterval(recordInterval);
              
    //             // send id to query to get question 
    //             // if (bioId) {
    //             //     props.questions.refetch({
    //             //         variables: {
    //             //             id: bioId
    //             //         }
    //             //     }).then((res) => {
    //             //         if(res.data){
    //             //             if(that.state.firstQuestion === undefined){
    //             //                 that.setState({ ...that.state, questions: res.data.bioYours, firstQuestion: res.data.bioYours.questions[0] ,  enableWebCam : 1});
    //             //             }else{
    //             //                 that.setState({ ...that.state, questions: res.data.bioYours ,  enableWebCam : 1 });
    //             //             }
    //             //         }
    //             //     })
    //             // }
    //         }
    //     }, 1000);
    // }
    // componentDidMount() {
    //      this.getQuestion(this.props, this.state);    
    // };
    componentWillReceiveProps(props){
            if(props.data){
                // console.log(props.data);
                this.setState({ ...this.state, questions:props.data, firstQuestion: props.data.questions[0] , enableWebCam : 1 });
                
        }
    }

    render() {
        // console.log(this.state.enableWebCam)
        if (!this.state.questions || !this.state.firstQuestion) {
            return (
                <div><h5 className="text-dark">Loading....</h5></div>
            )
        } else {
            let firstQuestion = this.state.firstQuestion;
            return (
                <div className="record">
                    <Container fluid className="m-0 p-0">
                        <Row className="m-0">
                            <Col xs={12} className="p-2">
                                <div>
                                    <div className="title">
                                        <h4>{firstQuestion.title}</h4>
                                    </div>
                                    <Video 
                                        data={firstQuestion}   
                                        enableWebCam={this.state.enableWebCam}
                                    />
                                </div>
                            </Col>
                            {this.state.questions.questions.map(question => {
                                if (question.video) {
                                    return (
                                        <Col  key={question.id} data-id={question.id} className="p-2 card-question" sm={3}  xs={6}>
                                            <button
                                                className="card-container"
                                                key={question.id}
                                                onClick={() => {
                                                    this.setState({ ...this.state, firstQuestion: question })
                                                }}>
                                                <Card className="border-0">
                                                    <Card.Img  src={question.video.frame.url} alt ="video poster" className="rounded" width="100%"/>
                                                    {/* <div className="bg-grey w-100 p-4">
                                                        <CropOriginalIcon className="text-grey" style={{ fontSize: "45px" }} />
                                                    </div> */}
                                                    <div className="success d-none" data-uploaded={question.id}>
                                                        <CheckCircleIcon style={{ fontSize: "21px", color: "#2ab72a" }} />
                                                    </div>
                                                    <Row className="m-0 pt-2 pb-2">
                                                        <Col xs={10} className="text-left p-0">{question.title}</Col>
                                                        {/* <Col xs={2} className="text-right p-0"><img src={dots} alt="dots" /></Col> */}
                                                    </Row>
                                                </Card>
                                            </button>
                                        </Col>
                                    )
                                }
                                else {
                                    return (
                                        <Col key={question.id} data-id={question.id} className="p-2 card-question" sm={3} xs={6}>
                                            <button
                                                className="card-container"
                                                key={question.id}
                                                onClick={() => {
                                                    this.setState({ ...this.state, firstQuestion: question });
                                                    window.scrollTo({ top: 0, behavior: 'smooth' });
                                                }}>

                                                <Card className="border-0">
                                                    <div className="bg-grey w-100 p-4">
                                                        <CropOriginalIcon className="text-grey" style={{ fontSize: "45px" }} />
                                                    </div>
                                                     {/* <img src={imgPlaceHolder} alt ="video poster" className="rounded" width="100%"/> */}
                                                    <div className="success d-none" data-uploaded={question.id}>
                                                        <CheckCircleIcon style={{ fontSize: "21px", color: "#2ab72a" }} />
                                                    </div>
                                                    <Row className="m-0 pt-2 pb-2">
                                                        <Col xs={10} className="text-left p-0">{question.title}</Col>
                                                        {/* <Col xs={2} className="text-right p-0"><img src={dots} alt="dots" /></Col> */}
                                                    </Row>
                                                </Card>
                                            </button>
                                        </Col>
                                    )
                                }
                            }
                            )}
                            <Col xs={12} className="p-2 pb-1">
                                <Typography variant="subtitle1" className="text-danger">{this.state.errorMessage}</Typography>
                            </Col>
                        </Row>
                    </Container>
                    <div className="w-100">
                        <Col xs="6" className="d-inline-block p-2  pt-4 text-left">
                            <Button type="button" 
                            size="large" 
                            onClick={()=>{
                                this.setState({ ...this.state, enableWebCam : 0 });
                                let list = document.getElementById("left-tabs-example");
                                let nav_items = list.querySelectorAll(".nav-item a");
                                nav_items[1].click();
                            }}
                            className="btn-white-outline pr-5 pl-5"
                            variant="contained" 
                             >Back</Button>
                        </Col>
                        <Col xs="6" className="d-inline-block p-2 pt-4 text-right">
                            <Button type="submit"
                                onClick={() => {
                                    // check if all questions have videos 
                                    let flag = [] ;
                                    this.state.questions.questions.map(question => {
                                        if (!question.video) {
                                             flag.push(0);
                                        }
                                        else {
                                            flag.push(1);
                                        }
                                    })
                                   if (flag.includes(0)){
                                        this.setState({...this.state,   errorMessage : 'You must record a video for each question.'})
                                        return false;
                                   }
                                   else{
                                        // go to next form  and make it not dissappled !
                                        this.setState({...this.state,   errorMessage : ''})
                                        window.scrollTo({ top: 0, behavior: 'smooth' });
                                        let list = document.getElementById("left-tabs-example");
                                        let nav_items = list.querySelectorAll(".nav-item a");
                                        nav_items[3].click();
                                        return true;
                                   }
                                }} size="large" className="btn-orange-gradiant pr-5 pl-5" variant="contained" >Next</Button>
                        </Col>
                    </div>
                </div>
            );
        }
    }
}

const GET_QUESTIONS = gql`
query($id : Int !){
    bioYours(id: $id){
        id
        title
        brif
        is_Draft
        created_at
        updated_at
        questions
        { id    
          title
          maximum_time
          video{
            url
            frame{
              url
            }
          }
        }
        __typename
    }
  }
`


export default compose(
    graphql(GET_QUESTIONS, {
        name: "questions"
    })
)(withRouter(Record));
