import React, { Component } from 'react';
import { createApolloFetch } from 'apollo-fetch';
import { withRouter } from 'react-router';
import { graphql } from 'react-apollo';
import * as compose from 'lodash.flowright';
import { gql } from 'apollo-boost';
import { Row, Col, Card, Modal } from 'react-bootstrap';
import { Paper, Button } from '@material-ui/core';
import { Link } from 'react-router-dom';
import PlayCircleFilledIcon from '@material-ui/icons/PlayCircleFilled';
import PlayCircleOutlineIcon from '@material-ui/icons/PlayCircleOutline';
import dots from '../record/images/more.png';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';


class CardBiography extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }
    componentDidMount() {
    }

    handleClose = () => this.setState({ ...this.state, show: false });
    handleShow = () => this.setState({ ...this.state, show: true });
    handleCloseDeleteModal = () => {this.setState({ ...this.state, showDeleteModal: false })}
    handleClick = (e) => {
        let elem = e.currentTarget;
        if (elem.querySelector('.custom-list')) {
            elem.querySelector('.custom-list').classList.toggle('d-none')
        }
    }
    render() {
        if (!this.props.data[0]) {
            return (
                <Row className="m-0 p-3 pt-4">
                    <Col className="text-center text-grey" sm={12}>
                        <h5>You have no biographies</h5>
                    </Col>
                </Row>
            )
        }
        else {
           
            return (
                <Row className="m-0 p-3 pt-4 ">
                    {
                        this.props.data.map(bio => {
                            return (
                                <Col className="p-3 " md='4'>
                                    <Card className="bg-transparent text-dark border-0 biographies">
                                        <Paper elevation={3} className="play-icon-container">
                                            <button type="button"
                                                className="bg-transparent border-0 p-0 play-icon-container"
                                                onClick={e => {
                                                    this.setState({ ...this.state, bioSelected: bio, firstQuestion: bio.questions[0], show: true })
                                                }}>
                                                <Card.Img src={bio.questions[0].video.frame.url} alt="Card image" />
                                                <div className="text-white play-icon row m-0 align-items-center">
                                                    <PlayCircleFilledIcon className="col" style={{ fontSize: '50px' }} />
                                                </div>
                                            </button>
                                        </Paper>
                                        <Row className="m-0">
                                            <Col xs={10} className="text-left p-0 bold mt-3">{bio.title}</Col>
                                            {/* <Col xs={6} className="text-right p-0 bold">
                                            <button className="bg-transparent border-0 p-0"></button></Col> */}
                                            <Col xs={2} className="text-right p-0 bold">
                                                <button
                                                    className="dots-btn offset-3"
                                                    onClick={this.handleClick}
                                                >
                                                    <p className="mb-0 mt-1 font-21">...</p>
                                                    <ul className="shadow custom-list custom-position d-none">
                                                        <li className="list-style-none text-left">
                                                            <Link to={{
                                                                pathname : '/createBiography',
                                                                state : {
                                                                    data : bio ,
                                                                    status : "Save Changes"
                                                                }
                                                            }}>
                                                                <button
                                                                className="bg-transparent border-0 p-0"
                                                                type="button">
                                                                <span className="pr-1"><EditIcon  style={{fontSize : "1rem"}} /></span> Edit Biography</button>
                                                            </Link>
                                                        </li>
                                                        <li className="list-style-none ">
                                                            <button
                                                                className="bg-transparent border-0 p-0 w-100 text-left"
                                                                type="button"
                                                                onClick={e => {
                                                                    this.setState({ ...this.state, bioSelected: bio, showDeleteModal: true })
                                                                }}>
                                                                <span className="pr-2"><DeleteIcon   style={{fontSize : "1rem"}} /></span>Delete </button></li>
                                                    </ul>
                                                </button>
                                            </Col>
                                        </Row>
                                        <Row className="m-0 pt-1  text-grey">
                                            <Col xs={7} className="text-left p-0"><span className="text-orange">{bio.speciality.name_en} </span><br />{bio.questions[0].video.created_at.substring(0, 10)}</Col>
                                            <Col xs={5} className="text-right p-0">{bio.questions[0].maximum_time} Sec</Col>
                                        </Row>
                                    </Card>
                                </Col>
                            )
                        })
                    }
                    {
                        (this.state.firstQuestion) ?
                            <Modal
                                size="xl"
                                aria-labelledby="contained-modal-title-vcenter"
                                centered
                                className="row m-0 modal-video pr-0 biographies"
                                show={this.state.show}
                                onHide={this.handleClose}>
                                <Col xs={12} className="p-0">
                                    <Modal.Body className="row m-0 p-0  bg-white">
                                        <Col xs={8} className="text-left p-0 bold p-2 pt-3 pb-3 captialize order-sm-1 order-1">
                                            <h6>{this.state.firstQuestion.title}</h6>
                                        </Col>
                                        <Col sm={3} xs={11} className="text-left  bold pr-0 pt-3 pb-3 pl-2  captialize bg-lightgrey order-sm-2 border-b-1 order-3">
                                            <h6>Questions</h6>
                                        </Col>
                                        <Col xs={1} className=" text-right pl-0  pr-2 pt-3 pb-3 order-sm-3 bg-lightgrey order-4 border-b-1">
                                            <button
                                                className="dots-btn offset-3"
                                                onClick={this.handleClick}
                                            >
                                                <img src={dots} alt="dots" />
                                                {/* <ul className="shadow custom-list d-none">
                                                    <li className="list-style-none text-left"><span><EditIcon className='pr-1' /></span> Edit Biography</li>
                                                    <li className="list-style-none text-left"><span><DeleteIcon className='pr-1' /></span>Delete</li>
                                                </ul> */}
                                                <ul className="shadow custom-list  d-none">
                                                        <li className="list-style-none text-left">
                                                            <Link to={{
                                                                pathname : '/createBiography',
                                                                state : {
                                                                    data : this.state.bioSelected ,
                                                                    status : "Save Changes"
                                                                }
                                                            }}>
                                                                <button
                                                                className="bg-transparent border-0 p-0"
                                                                type="button">
                                                                <span className="pr-1"><EditIcon  style={{fontSize : "1rem"}}/></span> Edit Biography</button>
                                                            </Link>
                                                        </li>
                                                        <li className="list-style-none ">
                                                            <button
                                                                className="bg-transparent border-0 p-0 w-100 text-left"
                                                                type="button"
                                                                onClick={e => {
                                                                    this.setState({ ...this.state, bioSelected: this.state.bioSelected, showDeleteModal: true })
                                                                }}>
                                                                <span className="pr-2"><DeleteIcon  style={{fontSize : "1rem"}} /></span>Delete </button></li>
                                                    </ul>
                                            </button>
                                        </Col>
                                        <Col sm={8} xs={12} className="p-0 order-sm-4 order-2 ">
                                            <iframe className="custom-iframe" title={this.state.firstQuestion.title} src={this.state.firstQuestion.video.url} width="100%" height="403rem"></iframe>
                                        </Col>
                                        <Col sm={4} xs={12} className="bg-lightgrey order-sm-5 order-5">
                                            {
                                                this.state.bioSelected.questions.map(question =>
                                                        (question.video !== null)?
                                                            <button type="button"
                                                            className="bg-transparent border-0 p-0 play-icon-container rounded"
                                                            onClick={e => {
                                                                this.setState({ ...this.state, firstQuestion: question })
                                                            }}>
                                                            <Row className="m-0 align-items-center pt-3 pb-3">
                                                                <Col xs={4} className="p-0">
                                                                    <img src={question.video.frame.url} alt="video poster" className="rounded" width="100%" />
                                                                    <div className="text-white play-icon  row m-0 align-items-center">
                                                                        <PlayCircleOutlineIcon className="col"/>
                                                                    </div>
    
                                                                </Col>
                                                                <Col xs={8} className="p-0  text-left">
                                                                    <p className="d-inline-block pl-2 captialize">
                                                                        {question.title}
                                                                    </p>
                                                                </Col>
                                                            </Row>
                                                        </button> : ''
                                                            )
                                            }
                                        </Col>
                                    </Modal.Body>
                                </Col>
                            </Modal>
                            : ''
                    }
                    <Modal
                        size="md"
                        centered
                        className="row m-0 custom-modal-delete pr-0"
                        show={this.state.showDeleteModal}
                        onHide={this.handleCloseDeleteModal}>
                        <Col xs={12} className="p-0">
                            <Modal.Body className="p-1">
                                <h5>Are you sure you want to delete this biography ?</h5>
                            </Modal.Body>
                            <Modal.Footer className="custom pt-3 pl-0 pr-0 pb-0">
                                <Row className="m-0 w-100">
                                    <Col xs={6} className="text-left p-0">
                                        <Button
                                            type="button"
                                            size="large"
                                            className="btn-white-outline  pr-5 pl-5" variant="contained"
                                            onClick={() => {
                                                this.handleCloseDeleteModal();
                                            }}>
                                            No
                                            </Button>
                                    </Col>
                                    <Col xs={6} className="text-right p-0">
                                        <Button
                                            type="button"
                                            size="large"
                                            className="btn-orange-gradiant pr-4 pl-4" variant="contained"
                                            onClick={() => {
                                                this.props.delete_bio({
                                                    variables: {
                                                        "bio_id": this.state.bioSelected.id
                                                    }
                                                }).then((res) => {
                                                    if (res.data.delete_bio.Status.code === 200) {
                                                        
                                                    }
                                                }).catch((err) => this.setState({ ...this.state, ConnectionErorr: "Check your internet connection" }));
                                                this.handleClose();
                                                this.handleCloseDeleteModal();
                                               
                                            }}>
                                            Yes, Delete
                                            </Button>
                                    </Col>
                                </Row>
                            </Modal.Footer>
                        </Col>
                    </Modal>
                </Row>
            )
        }
    }
}
const DELETE_BIO = gql`
mutation($bio_id : Int !){
    bioDelete(Inputs: {
      id : $bio_id
    }){
      Status{
        code
      }
      __typename
    }
  }
  
`
export default compose(
    graphql(DELETE_BIO, {
        name: "delete_bio"
    }),
)(withRouter(CardBiography));